import React, { useEffect, useState } from 'react';
import { useSwipeable } from 'react-swipeable';
import styles from './Home.module.scss';

const Home = () => {
  const [books, setBooks] = useState([]);
  const [selectedBookIndex, setSelectedBookIndex] = useState(0);

  useEffect(() => {
    // localStorage    
    const savedBooks = JSON.parse(localStorage.getItem('savedBooks')) || [];
    setBooks(savedBooks);

    // API    
    const fetchBookInfo = async () => {
      try {
        const user_seq = localStorage.getItem('user_seq');  // localStorage user_seq 
        const response = await fetch(`${process.env.REACT_APP_API_URL}/api/book/searchBookInfo?user_seq=${user_seq}&reading_status=0`, {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
          },
        });

        const data = await response.json();
        console.log(" : ", data);

        if (data && data.data && data.data.bookList) {
          setBooks(data.data.bookList);
        } else {
          console.error('  .');
        }
      } catch (error) {
        console.error('     :', error);
      }
    };

    fetchBookInfo();
  }, []);

  const handlers = useSwipeable({
    onSwipedLeft: () => {
      setSelectedBookIndex((prevIndex) => (prevIndex + 1) % books.length); //    
    },
    onSwipedRight: () => {
      setSelectedBookIndex((prevIndex) =>
        (prevIndex - 1 + books.length) % books.length  //    
      );
    },
    trackMouse: true,
  });

  const getDisplayedBooks = () => {
    const prevIndex = (selectedBookIndex - 1 + books.length) % books.length;
    const nextIndex = (selectedBookIndex + 1) % books.length;

    return [
      books[prevIndex],
      books[selectedBookIndex], //   
      books[nextIndex],
    ];
  };

  return (
    <div
      className={styles.homeContainer}
      {...handlers}
      style={{
        backgroundImage: `url('/assets/images/background.png')`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        height: '100vh'
      }}
    >
      <div className={styles.bookScrollContainer}>
        <div className={styles.bookScroll}>
          {getDisplayedBooks().map((book, index) => (
            <div
              key={book?.book_seq || index}
              className={`${styles.bookItem} ${index === 1 ? styles.selected : ''}`} //   'selected'  
            >
              <img src={book?.book_cover} alt={`  ${index + 1}`} className={styles.bookCover} />
            </div>
          ))}
        </div>
      </div>

      <div className={styles.statusContainer}>
        <button className={styles.statusButton}>
          {books[selectedBookIndex]?.reading_status === 0
            ? ' '
            : books[selectedBookIndex]?.reading_status === 1
            ? ' '
            : ' '}
        </button>
      </div>

      <div className={styles.progressBar}>
        <div
          className={styles.progress}
          style={{
            width: `${
              books[selectedBookIndex]?.reading_status === 0
                ? 0
                : books[selectedBookIndex]?.reading_status === 1
                ? 50
                : books[selectedBookIndex]?.reading_status === 2
                ? 100
                : 0
            }%`,
          }}
        ></div>
      </div>

      {books.length > 0 && (
        <div className={styles.bookInfo}>
          <div className={styles.infoRow}>
            <span className={styles.label}> </span>
            <span>{books[selectedBookIndex].title}</span>
          </div>
          <div className={styles.infoRow}>
            <span className={styles.label}></span>
            <span>{books[selectedBookIndex].publisher}</span>
          </div>
          <div className={styles.infoRow}>
            <span className={styles.label}>ISBN</span>
            <span>{books[selectedBookIndex].isbn}</span>
          </div>
          <div className={styles.infoRow}>
            <span className={styles.label}></span>
            <span>{books[selectedBookIndex].pages}</span>
          </div>
        </div>
      )}
    </div>
  );
};

export default Home;