import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import styles from './SearchBooks.module.scss';

const SearchBooks = () => {
  const navigate = useNavigate();
  const [searchQuery, setSearchQuery] = useState(''); 
  const [searchResults, setSearchResults] = useState([]);
  const [isSearching, setIsSearching] = useState(false);
  const [selectedBook, setSelectedBook] = useState(null);
  const [readingStatus, setReadingStatus] = useState(0);
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [showPopup, setShowPopup] = useState(false);

  const handleBackClick = () => {
    navigate(-1);
  };

  const token = localStorage.getItem('kakao_token');
  if (!token) {
    alert(" .");
    navigate('/login');  //       
  }
  


  //     
  const handleBookClick = (book) => {
    setSelectedBook(book);
    setShowPopup(true); //  
  };
  
  // API 
  const fetchBooks = async (query) => {
    setIsSearching(true); //  
    try {
      const token = localStorage.getItem('kakao_token');  // kakao_token 
      console.log(" : ", token);  //    

      if (!token) {
        throw new Error("  . .");
      }

      const response = await fetch(`http://localhost:8080/api/book/searchBook?title=${query}&startPage=1&maxResults=50`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`  // JWT   
        },
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();

      if (data && data.data && data.data.item) {
        setSearchResults(data.data.item); //   
      } else {
        setSearchResults([]); //      
      }
    } catch (error) {
      console.error('     :', error);
    } finally {
      setIsSearching(false); //  
    }
  };


const handleSaveReadingStatus = async () => {
  const token = localStorage.getItem('kakao_token');  //  
  if (!token) {
    alert(" .");
    navigate('/login');  //   
    return;
  }

  //    payload 
  const readingStatusPayload = {
    reading_seq: selectedBook.isbn,
    reading_status: readingStatus,
    title: selectedBook.title,
    description: selectedBook.description,
    publisher: selectedBook.publisher,
    author: selectedBook.author,
    isbn: selectedBook.isbn,
    pages: selectedBook.pages || 0,
    book_cover: selectedBook.cover,
    start_date: startDate,
    end_date: endDate
  };

  //   localStorage 
  const savedBooks = JSON.parse(localStorage.getItem('savedBooks')) || [];
  savedBooks.push(readingStatusPayload);
  localStorage.setItem('savedBooks', JSON.stringify(savedBooks));

  try {
    const response = await fetch('http://localhost:8080/api/readingStatus/status', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`,
      },
      body: JSON.stringify(readingStatusPayload),
    });

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const data = await response.json();
    console.log('   :', data);
    alert('  .');
    setShowPopup(false);  //    
  } catch (error) {
    console.error('     :', error);
  }
};


  //    
  const handleInputChange = (e) => {
    setSearchQuery(e.target.value);
  };

  //   
  const handleKeyPress = (e) => {
    if (e.key === 'Enter' && searchQuery) {
      fetchBooks(searchQuery);  // API 
    }
  };

  return (
    <div className={styles.searchBooks}>
      <div className={styles.header}>
        <button className={styles.backButton} onClick={handleBackClick}>
          <img src="/assets/icons/arrow.png" alt="" className={styles.icon} /> 
        </button>
        <div className={styles.searchBar}>
          <img src="/assets/icons/c_search.png" alt="" className={styles.icon} />
          <input 
            type="text" 
            value={searchQuery} 
            onChange={handleInputChange} 
            onKeyPress={handleKeyPress} 
          />
        </div>
      </div>

      {isSearching ? (
        <p> ...</p>
      ) : (
        <div className={styles.results}>
          {searchResults.length > 0 ? (
            searchResults.map((book, index) => (
              <div key={index} className={styles.bookItem} onClick={() => handleBookClick(book)}>
                <img src={book.cover} alt={book.title} className={styles.bookCover} />
                <div className={styles.bookDetails}>
                  <h3>{book.title}</h3>
                  <p>{book.author}</p>
                </div>
              </div>
            ))
          ) : (
            <p> </p>
          )}
        </div>
      )}

     {/*  */}
      {showPopup && selectedBook && (
        <div className={styles.popup}>
          <div className={styles.popupContent}>
            <button className={styles.closeButton} onClick={() => setShowPopup(false)}>X</button>
            
            <h2> </h2>
            <div className={styles.statusButtons}>
              <button className={readingStatus === 0 ? styles.active : ''} onClick={() => setReadingStatus(0)}> </button>
              <button className={readingStatus === 1 ? styles.active : ''} onClick={() => setReadingStatus(1)}> </button>
              <button className={readingStatus === 2 ? styles.active : ''} onClick={() => setReadingStatus(2)}> </button>
            </div>
            
            <h2> </h2>
            <div className={styles.datePicker}>
              <label> </label>
              <input type="date" value={startDate} onChange={(e) => setStartDate(e.target.value)} />
            </div>
            <div className={styles.datePicker}>
              <label> </label>
              <input type="date" value={endDate} onChange={(e) => setEndDate(e.target.value)} />
            </div>
            
            <button className={styles.saveButton} onClick={handleSaveReadingStatus}></button>
          </div>
        </div>
      )}

    </div>
  );
};

export default SearchBooks;