import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import styles from './LoginMain.module.scss';

const LoginMain = () => {
  const navigate = useNavigate();

  //  OAuth  URL
  const kakaoOAuthUrl = 'https://bookdochilsung.site/oauth2/authorization/kakao';

  //     
  const handleKakaoLogin = () => {
    window.location.href = kakaoOAuthUrl; //   OAuth  
  };

  useEffect(() => {
    // URL 'code'   
    const urlParams = new URLSearchParams(window.location.search);
    const code = urlParams.get('code');

    if (code) {
      console.log("  . : ", code);

      //  POST      
      fetch('/api/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({}) //   
      })
      .then((response) => response.json())
      .then((data) => {
        console.log(" : ", data);

        //   localStorage 
        localStorage.setItem('token', data.access_token);

        //   
        navigate('/');
      })
      .catch((error) => {
        console.error("    : ", error);
      });
    }
  }, []);

  return (
    <div 
      className={styles.loginMain} 
      style={{ 
        backgroundImage: `url('/assets/images/splash.png')`,  // scss        
        backgroundSize: 'cover', 
        backgroundPosition: 'center', 
        backgroundRepeat: 'no-repeat',
        height: '100vh' 
      }}
    >
      <button className={styles.kakaoButton} onClick={handleKakaoLogin}>
        <img src="/assets/icons/kakao.png" alt=" " className={styles.kakaoIcon} />
          
      </button>
    </div>
  );
};

export default LoginMain;