import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import styles from './BookShelf.module.scss';

const BookShelf = () => {
  const [filter, setFilter] = useState('');
  const [books, setBooks] = useState([]);
  const [filteredBooks, setFilteredBooks] = useState([]); //    
  const navigate = useNavigate();

  useEffect(() => {
    const savedBooks = JSON.parse(localStorage.getItem('savedBooks')) || [];
    setBooks(savedBooks);
    setFilteredBooks(savedBooks); //      
  }, []);

  const handleBackClick = () => {
    navigate(-1);
  };

  //  
  const handleFilterClick = (filterType) => {
    setFilter(filterType);

    let filtered = books; //    

    if (filterType === ' ') {
      filtered = books.filter(book => book.reading_status === 0);
    } else if (filterType === ' ') {
      filtered = books.filter(book => book.reading_status === 1);
    } else if (filterType === ' ') {
      filtered = books.filter(book => book.reading_status === 2);
    }

    setFilteredBooks(filtered); //    
  };

  return (
    <div className={styles.searchBooks}>
      <div className={styles.header}>
        <button className={styles.backButton} onClick={handleBackClick}>
          <img src="/assets/icons/arrow.png" alt="" className={styles.icon} />
        </button>
        <div className={styles.searchBar}>
          <img src="/assets/icons/c_search.png" alt="" className={styles.icon} />
          <input type="text" />
        </div>
      </div>

      {/*  */}
      <div className={styles.filters}>
        <button 
          className={filter === '' ? styles.active : ''} 
          onClick={() => handleFilterClick('')}
        ></button>
        <button 
          className={filter === ' ' ? styles.active : ''} 
          onClick={() => handleFilterClick(' ')}
        > </button>
        <button 
          className={filter === ' ' ? styles.active : ''} 
          onClick={() => handleFilterClick(' ')}
        > </button>
        <button 
          className={filter === ' ' ? styles.active : ''} 
          onClick={() => handleFilterClick(' ')}
        > </button>
      </div>

      <div className={styles.bookList}>
        {filteredBooks.map((book, index) => (
          <div className={styles.bookCard} key={index}>
            <img src={book.book_cover} alt={book.title} className={styles.bookCover} />
            <div className={styles.bookInfo}>
              <h3>{book.title}</h3>
              <p>{book.start_date} - {book.end_date}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default BookShelf;
